/**
 * File System Access API    
 * Chrome/Edge 
 */

// IndexedDB    DB 
const DB_NAME = 'recipe-app-fs'
const STORE_NAME = 'directory-handles'
const HANDLE_KEY = 'data-directory'

//   
export const FILE_NAMES = {
  RECIPES: 'recipes.json',
  SETTINGS: 'settings.json',
  PRESETS: 'presets.json'
} as const

export type FileType = keyof typeof FILE_NAMES

// File System Access API   
export function isFileSystemAccessSupported(): boolean {
  return 'showDirectoryPicker' in window
}

// IndexedDB    
async function getStoredDirectoryHandle(): Promise<FileSystemDirectoryHandle | null> {
  return new Promise((resolve, reject) => {
    const request = indexedDB.open(DB_NAME, 1)

    request.onerror = () => resolve(null)

    request.onupgradeneeded = (event) => {
      const db = (event.target as IDBOpenDBRequest).result
      if (!db.objectStoreNames.contains(STORE_NAME)) {
        db.createObjectStore(STORE_NAME)
      }
    }

    request.onsuccess = (event) => {
      const db = (event.target as IDBOpenDBRequest).result
      try {
        const transaction = db.transaction(STORE_NAME, 'readonly')
        const store = transaction.objectStore(STORE_NAME)
        const getRequest = store.get(HANDLE_KEY)

        getRequest.onsuccess = () => {
          resolve(getRequest.result || null)
        }
        getRequest.onerror = () => resolve(null)
      } catch {
        resolve(null)
      }
    }
  })
}

// IndexedDB   
async function storeDirectoryHandle(handle: FileSystemDirectoryHandle): Promise<void> {
  return new Promise((resolve, reject) => {
    const request = indexedDB.open(DB_NAME, 1)

    request.onerror = () => reject(new Error('IndexedDB  '))

    request.onupgradeneeded = (event) => {
      const db = (event.target as IDBOpenDBRequest).result
      if (!db.objectStoreNames.contains(STORE_NAME)) {
        db.createObjectStore(STORE_NAME)
      }
    }

    request.onsuccess = (event) => {
      const db = (event.target as IDBOpenDBRequest).result
      const transaction = db.transaction(STORE_NAME, 'readwrite')
      const store = transaction.objectStore(STORE_NAME)
      store.put(handle, HANDLE_KEY)

      transaction.oncomplete = () => resolve()
      transaction.onerror = () => reject(new Error('  '))
    }
  })
}

// IndexedDB   
async function clearDirectoryHandle(): Promise<void> {
  return new Promise((resolve) => {
    const request = indexedDB.open(DB_NAME, 1)

    request.onsuccess = (event) => {
      const db = (event.target as IDBOpenDBRequest).result
      try {
        const transaction = db.transaction(STORE_NAME, 'readwrite')
        const store = transaction.objectStore(STORE_NAME)
        store.delete(HANDLE_KEY)
        transaction.oncomplete = () => resolve()
        transaction.onerror = () => resolve()
      } catch {
        resolve()
      }
    }

    request.onerror = () => resolve()
  })
}

//    
async function verifyPermission(
  handle: FileSystemDirectoryHandle,
  mode: 'read' | 'readwrite' = 'readwrite'
): Promise<boolean> {
  const options = { mode }

  // @ts-ignore - queryPermission  TypeScript  
  if ((await handle.queryPermission(options)) === 'granted') {
    return true
  }

  // @ts-ignore - requestPermission  TypeScript  
  if ((await handle.requestPermission(options)) === 'granted') {
    return true
  }

  return false
}

/**
 * FileSystemStorage 
 *       
 */
export class FileSystemStorage {
  private directoryHandle: FileSystemDirectoryHandle | null = null
  private initialized = false

  /**
   *   -     
   */
  async initialize(): Promise<boolean> {
    if (!isFileSystemAccessSupported()) {
      console.log('File System Access API  .')
      return false
    }

    try {
      const storedHandle = await getStoredDirectoryHandle()
      if (storedHandle) {
        const hasPermission = await verifyPermission(storedHandle)
        if (hasPermission) {
          this.directoryHandle = storedHandle
          this.initialized = true
          return true
        }
      }
    } catch (error) {
      console.log('   :', error)
    }

    return false
  }

  /**
   *    
   */
  async selectDirectory(): Promise<string | null> {
    if (!isFileSystemAccessSupported()) {
      throw new Error('     . Chrome  Edge .')
    }

    try {
      // @ts-ignore
      const handle = await window.showDirectoryPicker({
        mode: 'readwrite',
        startIn: 'documents'
      })

      const hasPermission = await verifyPermission(handle)
      if (!hasPermission) {
        throw new Error('   .')
      }

      this.directoryHandle = handle
      this.initialized = true

      // IndexedDB   (   )
      await storeDirectoryHandle(handle)

      return handle.name
    } catch (error: any) {
      if (error.name === 'AbortError') {
        //  
        return null
      }
      throw error
    }
  }

  /**
   *     
   */
  getDirectoryName(): string | null {
    return this.directoryHandle?.name || null
  }

  /**
   *    
   */
  isDirectorySelected(): boolean {
    return this.initialized && this.directoryHandle !== null
  }

  /**
   *   
   */
  async disconnect(): Promise<void> {
    this.directoryHandle = null
    this.initialized = false
    await clearDirectoryHandle()
  }

  /**
   *   
   */
  async writeFile(fileType: FileType, data: any): Promise<void> {
    if (!this.directoryHandle) {
      throw new Error('   .')
    }

    const fileName = FILE_NAMES[fileType]

    try {
      const fileHandle = await this.directoryHandle.getFileHandle(fileName, { create: true })
      const writable = await fileHandle.createWritable()

      const jsonString = JSON.stringify(data, null, 2)
      await writable.write(jsonString)
      await writable.close()
    } catch (error) {
      console.error(`   (${fileName}):`, error)
      throw new Error(`  : ${fileName}`)
    }
  }

  /**
   *   
   */
  async readFile<T>(fileType: FileType): Promise<T | null> {
    if (!this.directoryHandle) {
      return null
    }

    const fileName = FILE_NAMES[fileType]

    try {
      const fileHandle = await this.directoryHandle.getFileHandle(fileName)
      const file = await fileHandle.getFile()
      const content = await file.text()

      return JSON.parse(content) as T
    } catch (error: any) {
      if (error.name === 'NotFoundError') {
        //   -  
        return null
      }
      console.error(`   (${fileName}):`, error)
      return null
    }
  }

  /**
   *    
   */
  async fileExists(fileType: FileType): Promise<boolean> {
    if (!this.directoryHandle) {
      return false
    }

    const fileName = FILE_NAMES[fileType]

    try {
      await this.directoryHandle.getFileHandle(fileName)
      return true
    } catch {
      return false
    }
  }

  /**
   *     
   */
  async listFiles(): Promise<string[]> {
    if (!this.directoryHandle) {
      return []
    }

    const files: string[] = []

    try {
      // @ts-ignore
      for await (const entry of this.directoryHandle.values()) {
        if (entry.kind === 'file' && entry.name.endsWith('.json')) {
          files.push(entry.name)
        }
      }
    } catch (error) {
      console.error('   :', error)
    }

    return files
  }
}

//  
export const fileSystemStorage = new FileSystemStorage()
